@import url('https://fonts.googleapis.com/css?family=Work+Sans:600" rel="stylesheet');
@value tablet, desktop from './Variables.css';

body {
  font-family: 'Work Sans', sans-serif;
  font-weight: bold;
  overflow-x: hidden;
}

.introText {
  height: 80vh;
  margin: auto;
  font-size: .9em;
  z-index: 3;
}

.sky {
  position: absolute;
  z-index: 3;
}

.appWrapper {
  height: 100%;
  position: absolute;
  bottom: 0;
}

.nameTitle, .subtitle {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  padding-left: 2em;
  text-shadow: 1px 1px 0px #00c0ff;
}

.nameTitle {
  padding-top: 2em;
}

.svgContainer {
  width: 100%;
  height: 100%;
}

@media only screen and (min-width: tablet) {
  .links {
    -ms-flex-direction: row;
        flex-direction: row;
    top: 50%;
    width: 100%;
    font-size: 1em;
  }
  .nameTitle, .subtitle {
    padding: 3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: left;
        justify-content: left;
    padding-left: auto;
    font-size: 1em;
    padding-top: 5em;
  }
  .introText {
    height: 100vh;
    margin: auto;
    font-size: .9em;
  }
}

.seeMore {
  color: white;
  position: fixed;
  margin: auto;
  text-align: center;
  padding-top: 2em;
  font-size: .9em;
  text-shadow: 1px 1px 0px #00c0ff;
  letter-spacing: 6px;
  -webkit-animation: 4.2s ease 0s normal infinite bounce, 5s ease 0s normal forwards 1 fadein;
          animation: 4.2s ease 0s normal infinite bounce, 5s ease 0s normal forwards 1 fadein;
  width: 100%;
  top: 50%;
  z-index: 0;
}

.seeMore svg {
  position: inherit;
}

@-webkit-keyframes fadein{
    0% { opacity:0; }
    66% { opacity:0;}
    100% { opacity:.8; }
}

@keyframes fadein{
    0% { opacity:0; }
    66% { opacity:0;}
    100% { opacity:.8; }
}

@-webkit-keyframes bounce{
    0% { -webkit-transform: translateY(-30px); transform: translateY(-30px); }
    66% { -webkit-transform: translateY(-30px); transform: translateY(-30px);}
    100% { -webkit-transform: translateY(-15px); transform: translateY(-15px); }
}

@keyframes bounce{
    0% { -webkit-transform: translateY(-30px); transform: translateY(-30px); }
    66% { -webkit-transform: translateY(-30px); transform: translateY(-30px);}
    100% { -webkit-transform: translateY(-15px); transform: translateY(-15px); }
}

svg {
  position: absolute;
}

@media only screen and (min-width: tablet) {
  .seeMore {
    font-size: 1.5em;
  }
}

@media only screen and (min-width: desktop) {
  .seeMore {
    -ms-flex-direction: row;
        flex-direction: row;
    width: 100%;
    padding-top: 6em;
    font-size: 2em;
    top: auto;
  }
  .nameTitle, .subtitle {
    font-size: 1.5em;
    padding: 3em;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: left;
        justify-content: left;
    padding-left: auto;
  }
}



